* {
  margin: 0;
  padding: 0;
  color: initial;
  text-decoration: none;
  font-size: xx-large;
  font-family: "Courier New", Courier, monospace;
}

body {
  display: grid;
}

#navbar {
  height: 3em;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1em;
  background-color: pink;
}
#navbar a:hover {
  color: bisque;
  transition: 0.15s;
}